.services {
    .slider-template-item {
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        align-items: flex-start;
        position: relative;
        &::before {
            content: "";
            position: absolute;
            top: 0;
            right: 0;
            bottom: 0;
            left: 0;
            background: linear-gradient(0deg, rgba(4, 5, 17, 0.91) 0%, rgba(88, 88, 88, 0) 100%);
            border-radius: 20px;
        }
    }
    .slider-template-item_description {
        margin-bottom: 14px;
    }
    .slider-template-item_title,
    .slider-template-item_description,
    .slider-template-item_buttons {
        z-index: 1;
    }
    
}